/* Basic reset */
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.25rem;
  background: linear-gradient(to right, #0b3d91 0%, #ff4da6 100%);
  color: #fff;
  border-bottom: 0;
  font-family: system-ui, -apple-system, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
}

.nav a {
  color: #fff;
  text-decoration: none;
  font-size: 1.05rem;
}

.nav__brand {
  font-weight: 700;
  letter-spacing: 0.5px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.nav__logo {
  width: 50px;
  height: 50px;
  border-radius: 4px;
  display: block;
}

.nav__menu a.is-active {
  text-decoration: none;
  color: #0b3d91;
  font-weight: 700;
}

.nav__menu {
  list-style: none;
  display: flex;
  gap: 1rem;
  margin: 0;
  padding: 0;
}

.btn {
  padding: 0.5rem 0.9rem;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.35);
}

.btn--primary {
  background: #ff6a00;
  border-color: #ff6a00;
  color: #fff;
}

/* Hamburger */
.nav__hamburger {
  display: none;
  cursor: pointer;
  background: transparent;
  border: 0;
  padding: 6px;
}

.nav__hamburger span {
  display: block;
  width: 24px;
  height: 2px;
  background: #fff;
  margin: 5px 0;
  transition: transform 0.3s ease, opacity 0.2s ease;
}

/* Responsive */
@media (max-width: 768px) {
  .nav {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .nav__brand {
    flex: 1 1 auto;
    align-items: center;
    order: 1;
  }

  .nav__hamburger {
    display: block;
    margin-left: 8px;
    order: 2;
  }

  .nav__menu {
    position: static;
    width: 100%;
    background: transparent;
    flex-direction: column;
    gap: 0;
    display: none;
    margin-top: 8px;
    flex: 1 0 100%;
    order: 3;
  }

  .nav__menu li {
    border-top: 1px solid rgba(255, 255, 255, 0.25);
  }

  .nav__menu li a {
    display: block;
    padding: 0.9rem 1rem;
  }

  .nav.nav--open .nav__menu {
    display: flex;
  }

  /* Hamburger -> Close (X) */
  .nav.nav--open .nav__hamburger span:nth-child(1) {
    transform: translateY(7px) rotate(45deg);
  }

  .nav.nav--open .nav__hamburger span:nth-child(2) {
    opacity: 0;
  }

  .nav.nav--open .nav__hamburger span:nth-child(3) {
    transform: translateY(-7px) rotate(-45deg);
  }
}