.joinus { font-size: 1.08rem; }
.joinus *, .joinus *::before, .joinus *::after { box-sizing: border-box; }
.joinus form { width: 100%; }
.joinus label { font-weight: 600; color: #374151; }
.joinus input, .joinus textarea { display: block; width: 100%; max-width: 100%; padding: 0.7rem 0.85rem; border: 1px solid #e5e7eb; border-radius: 8px; background: #fff; color: #111; }
.joinus input::placeholder, .joinus textarea::placeholder { color: #9ca3af; }
.joinus input:focus, .joinus textarea:focus { outline: none; border-color: #93c5fd; box-shadow: 0 0 0 3px rgba(59,130,246,0.15); }
.joinus button { font-size: 1rem; }


