.slider {
    position: relative;
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    overflow: hidden;
    border-radius: 0;
    box-shadow: none;
}

.slider__viewport {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 45%;
}

.slider__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
    user-select: none;
}

.slider__control {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.35);
    color: #fff;
    border: 0;
    width: 40px;
    height: 40px;
    border-radius: 20px;
    cursor: pointer;
    display: grid;
    place-items: center;
    font-size: 22px;
}

.slider__control--prev {
    left: 10px;
}

.slider__control--next {
    right: 10px;
}

.slider__control:hover {
    background: rgba(0, 0, 0, 0.5);
}

.slider__dots {
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    display: flex;
    gap: 8px;
    justify-content: center;
}

.slider__dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.6);
    border: 0;
    cursor: pointer;
}

.slider__dot.is-active {
    background: #ff6a00;
}