.footer {
    background: linear-gradient(to right, #0b3d91 0%, #ff4da6 100%);
    color: #fff;
    padding: 1.25rem;
    margin-top: 2rem;
    border-top: 0;
    font-family: system-ui, -apple-system, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
}

.footer__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    flex-wrap: wrap;
}

.footer__left {
    display: flex;
    align-items: center;
    gap: 16px;
    flex-wrap: wrap;
}

.footer__brand {
    font-weight: 700;
    letter-spacing: 0.5px;
}

.footer__brand {
    display: flex;
    align-items: center;
    gap: 8px;
}

.footer__logo {
    width: 50px;
    height: 50px;
    border-radius: 4px;
    display: block;
}

.footer__links {
    display: flex;
    gap: 1rem;
}

.footer__links a {
    color: #fff;
    text-decoration: none;
    opacity: 0.95;
    font-size: 1.02rem;
}

.footer__links a:hover {
    opacity: 1;
    text-decoration: underline;
}

.footer__copy {
    margin-top: 0.75rem;
    font-size: 0.9rem;
    opacity: 0.85;
    text-align: center;
}

.footer__socials {
    display: flex;
    gap: 10px;
    align-items: center;
}

.footer__icon {
    color: #fff;
    opacity: 0.95;
    display: inline-flex;
    width: 36px;
    height: 36px;
    border-radius: 18px;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.18);
}

.footer__icon:hover {
    opacity: 1;
    background: rgba(255, 255, 255, 0.25);
}

@media (max-width: 768px) {
    .footer__content {
        flex-direction: column;
        align-items: flex-start;
        gap: 12px;
    }

    .footer__left {
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
    }

    .footer__links {
        gap: 0.75rem;
    }

    .footer__socials {
        align-self: stretch;
        display: flex;
        gap: 10px;
    }
}