.initiative {
    background: #fff;
    border-radius: 12px;
    padding: 1.8rem;
    margin: 1.5rem 0;
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
    font-family: system-ui, -apple-system, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    line-height: 1.7;
  }
  
  .initiative h3 {
    color: #d63384;
    margin-bottom: 0.8rem;
  }
  
  .initiative h4 {
    margin-top: 1.2rem;
    color: #0b3d91;
  }
  
  .initiative ul {
    padding-left: 1.2rem;
    margin: 1rem 0;
  }
  
  .initiative ul li {
    margin-bottom: 0.6rem;
  }
  
  .initiative ul.topics li strong {
    color: #ff4da6;
  }
  
  .initiative ul.schools li {
    list-style: "üè´ ";
  }
  
  .initiative p.impact {
    margin-top: 1.2rem;
    font-weight: bold;
    font-size: 1.05rem;
    color: #333;
  }
  


.events {
    max-width: 1100px;
    margin: 2rem auto;
    padding: 0 1rem;
    text-align: left;
}