.events { max-width: 1100px; margin: 2rem auto; padding: 0 1rem; text-align: left; }
.events__header { display: flex; align-items: center; justify-content: space-between; gap: 1rem; margin-bottom: 1rem; }
.events__header h2 { margin: 0; }
.events__viewall { text-decoration: none; color: #0b3d91; font-weight: 600; }

.events__grid { display: grid; grid-template-columns: repeat(3, 1fr); gap: 16px; }
@media (max-width: 900px) { .events__grid { grid-template-columns: repeat(2, 1fr); } }
@media (max-width: 600px) { .events__grid { grid-template-columns: 1fr; } }

.event { background: #fff; border-radius: 10px; box-shadow: 0 6px 20px rgba(0,0,0,0.08); padding: 1rem; display: flex; flex-direction: column; }
.event__date { align-self: flex-start; background: #0b3d91; color: #fff; border-radius: 6px; padding: 0.25rem 0.5rem; font-size: 0.85rem; margin-bottom: 0.5rem; }
.event__title { margin: 0.25rem 0; }
.event__meta { color: #666; font-size: 0.95rem; margin-bottom: 0.5rem; }
.event__desc { margin: 0 0 0.75rem 0; }
.event__actions { display: flex; gap: 0.5rem; margin-top: auto; }
.btn { padding: 0.5rem 0.9rem; border-radius: 6px; border: 1px solid rgba(0,0,0,0.15); background: #f7f7f8; color: #0b3d91; text-decoration: none; }
.btn--primary { background: #ff6a00; border-color: #ff6a00; color: #fff; }


