.contact-container {
    max-width: 1000px;
    margin: 3rem auto;
    padding: 0 1rem;
    font-family: system-ui, -apple-system, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  }
  
  .contact-card {
    background: #fff;
    border-radius: 12px;
    padding: 2rem;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  }
  
  .contact-heading {
    margin-bottom: 0.5rem;
    color: #0b3d91;
    font-size: 2rem;
    font-weight: 600;
  }
  
  .contact-subtext {
    margin-bottom: 1.5rem;
    color: #444;
    font-size: 1rem;
    line-height: 1.6;
  }
  
  .contact-form {
    display: grid;
    gap: 14px;
    max-width: 600px;
  }
  
  .contact-input,
  .contact-textarea {
    padding: 0.8rem 1rem;
    border-radius: 8px;
    border: 1px solid #ccc;
    font-size: 1rem;
    transition: 0.3s;
  }
  
  .contact-textarea {
    height: 120px;
    resize: none;
  }
  
  .contact-input:focus,
  .contact-textarea:focus {
    border-color: #0b3d91;
    outline: none;
    box-shadow: 0 0 4px rgba(11, 61, 145, 0.4);
  }
  
  .contact-button {
    margin-top: 0.5rem;
    padding: 0.9rem 1.4rem;
    border-radius: 8px;
    background: linear-gradient(to right, #0b3d91, #ff4da6);
    color: #fff;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: 0.3s;
  }
  
  .contact-button:hover {
    opacity: 0.9;
  }
  